<template>  
  <div id="app">
    <div class="column is-half is-offset-one-quarter">
      <div class="alinhar">
        <a href="https://ret2021.github.io/pokedex-vue/">
          <img  src="./assets/pokedexlogo.png"> 
        </a>
        <input class="input is-info is-rounded" type="text" placeholder="Buscar Pokemon" v-model="busca">   
        <button @click="searchPoke" class="button  is-small is-success">Buscar</button>    
        <div id="pokes" v-for="(poke,index) in filteredPokes" :key="poke.url">
          <Poke :num="index + 1" :name="poke.name" :url="poke.url"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios"
import Poke from "./components/Poke.vue"

export default {
  name: 'App',
  data(){
    return{
      pokes:[],
      filteredPokes:[],
      busca:""
    }
  },
  components:{
    Poke
  },
  created:function(){
    axios.get("https://pokeapi.co/api/v2/pokemon?limit=151&offset=0").then(response =>{
      this.pokes = response.data.results    
      this.filteredPokes = response.data.results    
    }).catch(err =>{
      console.log(err)
    })
    
  },
  methods:{
    searchPoke:function(){
      this.filteredPokes = this.pokes
      if(this.busca == '' || this.busca == ''){
        this.filteredPokes = this.pokes
      }else{        
        this.filteredPokes = this.pokes.filter(poke => poke.name == this.busca)
      }
    }
  },
}
</script>


<style>
  .alinhar{
    display: flex;
    flex-direction: column;
    align-items: center;  
  }

  #pokes{
    width: 650px;
  }

  .input{
    margin-top: 50px;
    margin-bottom:20px ;
  }

  .button{
    width: 200px;

  }
</style>
