<template>
    <div >
        <div id="pokes" style="margin-top:30px" class="box" >
            <img v-if="isFront" :src="pokemon.front" >
            <img v-else :src="pokemon.back"  >
            <p style="font-size:21px">{{num}} - {{name | upper}}</p>
            <small>{{pokemon.type}}</small><br>
            <button  class="button is-small is-fullwidth" @click="changeSprite">Mudar Sprite</button>
        </div>
  </div>
</template>

<script>

import axios from "axios"

export default {
    data(){
        return{
            isFront : true,
            pokemon:{
                type:"",
                front:"",
                back:"",
            }
        }
    },
    created:function(){
        axios.get(this.url).then(response =>{
            this.pokemon.type = response.data.types[0].type.name
            this.pokemon.front = response.data.sprites.front_default;      
            this.pokemon.back = response.data.sprites.back_default;      
        }).catch(err =>{
            console.log(err)
        })
    },
    props:{
        num:Number,
        name:String,
        url:String
    },
    filters:{
        upper: function(value){
            let newName = value[0].toUpperCase() + value.slice(1)
            return newName
        }
    },
    methods:{
        changeSprite(){
            this.isFront = !this.isFront
        }
    }
   
}
</script>

<style scoped>

    #pokes{
        display: flex;
        flex-direction: column;
        align-items: center;     
    }
</style>